import React from 'react';
import './App.css';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: "tags"
    };
  }
  handleToggleClick(event) {
    
    this.setState({
      tab: true
    });
   
  }

  render() {
    return (
      <div className="App">
        <img src={"/images/LogoFullB.png"} className="App-logo" alt="logo" />
        <ToggleButtonGroup
          
          exclusive
          onChange={this.handleTab}
          aria-label="tabs"
        >
          <ToggleButton value="tags" aria-label="left aligned">
            Tags
      </ToggleButton>
          <ToggleButton value="recommend" aria-label="right aligned">
            Recommend
      </ToggleButton>
        </ToggleButtonGroup>
      </div>
    );
  }
}



